import s from './App.module.css';
import {Navbar} from "./component/navbar/Navbar";
import React, {Suspense} from 'react';
import {Routes, Route, HashRouter, Router, Redirect, Navigate} from "react-router-dom";
import {News} from "./component/news/news";
import Music from "./component/music/music";
import {Settings} from "./component/settings/settings";
import UsersContainer from "./component/users/usersContainer";
import {HeaderContainer} from "./component/header/headerContainer";
import {LoginPageApi} from "./component/login/LoginPage";
import {useEffect} from "react";
import {connect, Provider} from "react-redux";
import {initializedApp} from "./redux/appReducer";
import Preloader from './common/imgGif/load.gif'
import store from "./redux/reduxStore";
import {Profile} from "./component/profile/profile";

const DialogsContainer = React.lazy(() => import('./component/Dialogs/DialogsContainer'));
const ProfileContainer = React.lazy(() => import('./component/profile/profileContainer'));
// const Music = React.lazy(() => import('./component/music/music'));


const App = (props) => {
    useEffect(() => {
        props.initializedApp()
    }, [])
    if(!props.initialized) {
        return <img src={Preloader}/>
    }

    return (
            <div className={s.appWrapper}>
                <HeaderContainer/>
                <Navbar/>
                <div className={s.appWrapperContent}>                        {console.log(process.env.PUBLIC_URL, 1)}
                    {/*{console.log(process.env.PUBLIC_URL, 1)}*/}

                    <Suspense fallback={<div><img src={Preloader}/></div>}>
                        {/*<Router basename={process.env.PUBLIC_URL}>*/}
                    <Routes>
                        {/*<Route path="/*" element={<Navigate to="/demo_test_samurai/profile" />}  />*/}
                        {/*<Route exact path='/' element={<Na exact from='/' to={"/profile"} />} />*/}
                        {/*<Route path='/demo_test_samurai' element={<ProfileContainer/>}/>*/}
                        {/*<Route path='/' element={<Navigate to='/profile'/>}/>*/}
                        <Route path='/profile/*' element={<ProfileContainer/>}/>
                        <Route path='messages/*' element={<DialogsContainer/>}/>
                        <Route path='users' element={<UsersContainer/>}/>
                        <Route path='/login' element={<LoginPageApi />}/>
                        <Route path='news' element={<News/>}/>
                        <Route path='music' element={<Music/>}/>
                        <Route path='settings' element={<Settings/>}/>
                        {/*<Route path="/demo_test_samurai" element={<Profile />}  />*/}
                    </Routes>
                        {/*</Router>*/}
                        </Suspense>
                </div>
            </div>
    )
}

const mapStateToProps = (state) => ({
    initialized: state.app.initialized
})


const AppContainer = connect(mapStateToProps,{initializedApp})(App)


export const SamuraiJSApp = (props) => (
    <HashRouter>
        <Provider store={store}>
            <AppContainer />
        </Provider>
    </HashRouter>
)


